.body{
    display: flex;
    justify-content: center;
}

.content{
    margin-top: 60px ;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40%;
}

.appTitle {
    box-shadow: 0 0 5px 2px lightgray;
    background-color: rgba(250, 235, 215, 0.5);
    border-radius: 50px;
    padding: 40px 50px;
    margin: 20px;
    font-size: 2.5rem;
    color: white;
    text-shadow: 2px 2px 2px rgb(56, 55, 55);
    position: relative;
}

.appTitle > .shadow{
    color: rgba(87, 83, 83, 0.685);
    position: absolute;  
    left: 40px;
    top: 50px;
    z-index: 1;
}

.appTitle > .videoCamera{
    position: absolute;
    left: 38px;
    top: 48px;
    z-index: 2;
}

.appTitle > span{
    margin-left: 40px;
}

.searchBar{
    background-color: #FFFFFF33;
    color: black;
    border: solid 1px black;
    width: 100%;
    display: flex;
    align-items: center;
    border-radius: 30px;
    position: relative;
    height: 50px;
    cursor: pointer;
    margin: 20px;
}
.searchBar > input{
    background: none;
    display: flex;
    align-items: center;
    border: none;
    width: 82%;
    outline: none;
    padding: 10px 30px;
    font-size: 1.5rem;
}

.magnifyingGlass{
    position: absolute;
    padding: 14px;
    right: 10px;
    top: 0;
    font-size: 1.5rem;
}